<template>
    <yd-layout class="box_camera">

        <div class="box_form">
            <div class="box_cont" style="align-items: center;">
                <p class="mright">企业：</p>
                <div class="box_org" @click="showorg = true">
                    <input readonly="readonly" v-on:focus="heighOn"  :value="orginfo.QYMC" type="text" class="box_input" placeholder="请选择企业">
                    <div style="width: .5rem;height: .5rem;">
                        <img src="../../assets/down.png" alt="" style="width: 100%;height: 100%;">
                    </div>
                </div>
                <yd-popup v-model="showorg" position="bottom">
                    <div class="org_list">
                        <div v-for="item,index in orggroup" :key="index" @click="onClickOrgname(item)">{{item.QYMC}}</div>
                    </div>
                </yd-popup>
            </div>
            <div class="box_cont mtop">
                <p class="mright">隐患：</p>
                <textarea v-on:focus="heighOn" rows="5" class="box_txt" placeholder="请尽可能详细描述隐患情况" v-model="danger"></textarea>
            </div>
            <div class="box_cont mtop">
                <p class="mright">附件：</p>
                <unphoto v-on:imagesUrl="saveUrl"></unphoto>
            </div>
            <yd-button @click.native="saveData" size="large" color="#fff" bgcolor="#1D91E6">保存</yd-button>
        </div>
    </yd-layout>
</template>

<script>
    import unphoto from '../../components/unphoto.vue'
    import {api_camera,api_qiye_list} from "../../components/APIKeys";

    export default {
        name: "Camera",
        components:{unphoto},
        data(){
            return{
                orggroup:[],//企业列表数组
                showorg:false,//企业选择列表是否展开
                orginfo:[],//企业信息
                photoUrl:'',//保存子组件传过来的base64
                // token:window.localStorage.getItem('token'),
                danger:'',//隐患
            }
        },
        mounted(){
          this.doLoadOrglist();
        },
        methods:{
            //获取企业列表
            doLoadOrglist(){
                let jsonData = {  };
                this.$post(api_qiye_list, jsonData).then(res=>{
                    if(res.errno == 0){
                        this.orggroup = res.data || [];
                    }
                })
            },
            //点击选择企业
            onClickOrgname(org){
                this.showorg = false;
                this.orginfo = org || [];
            },
            //将子组件的值传过来
            saveUrl:function(data){
                this.photoUrl = data;
            },

            //点击保存
            saveData(){
                if(this.orginfo.QYMC == '' || this.orginfo.QYMC == undefined){
                    this.$showError('请选择企业');
                    return false;
                }else if(this.danger == ''){
                    this.$showError('请填写隐患');
                    return false;
                }else if(this.photoUrl == ''){
                    this.$showError('请上传隐患图片');
                    return false;
                }
                this.$showDialog();
                let jsonData = {
                    // token:this.token,
                    qiye_id:this.orginfo.QIYE_ID,
                    qiye_name:this.orginfo.QYMC,
                    remarks:this.danger,//备注
                    attachment:this.photoUrl,//附件
            };
                this.$post(api_camera, jsonData).then(res=>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.$showSuccess('保存成功');
                        this.$pop();
                    }else{
                        this.$showError('保存失败');
                    }
                })
            },
            //防止键盘遮挡
            heighOn: function() {
                if (/Android/gi.test(navigator.userAgent)) {
                    window.addEventListener('resize', function () {
                        if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                            window.setTimeout(function () {
                                document.activeElement.scrollIntoViewIfNeeded();
                            }, 0);
                        }
                    })
                }
            },
        }
    }
</script>

<style scoped>
    .box_camera{
        background: #fff;
    }
    .box_form{
        padding: .3rem;
    }
    .box_cont{
        display: flex;
        /*align-items: center;*/
    }
    .box_org{
        flex: 1;
        height: 0.8rem;
        border: 1px solid #d9d9d9;
        border-radius:5px;
        display: flex;
        align-items: center;
    }
    .box_input{
        height: 0.8rem;
        flex: 1;
        border: 0;
        padding: .2rem;
        border-radius:5px;
    }
    .box_txt{
        flex: 1;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        padding: .2rem;
    }
    .mtop{
        margin-top: .2rem;
    }
    .mright{
        margin-right: .2rem;
        white-space:nowrap;
    }
    .box_light{
        flex: 1;
        display: flex;
        align-items: center;
        flex-wrap:wrap;
    }
    .box_img{
        width: calc(95% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        flex-direction:column;
        align-items: center;
        text-align: center;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        margin-bottom: .1rem;

    }
    .box_img img{
        width: 60%;
        height:60%;
    }
    .box_photo{
        width: calc(93% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        margin-bottom: .1rem;
        position: relative;
    }
    .box_photo .img1{
        width: 100%;
        height: 100%;
    }
    .box_delete{
        position: absolute;
        right: 0;
        top: 0;
        width: 35%;
        height: 35%;
    }
    .org_list div{
        padding: .24rem;
        border-bottom: 1px solid #d9d9d9;
        text-align: center;
    }
</style>
<style>
    .box_camera .yd-popup-bottom{
        max-height:60% !important;
    }
</style>



